play: inline-block;\n        vertical-align: top;\n      }\n    }\n    th {\n      display: block;\n      text-align: right;\n    }\n    td {\n      display: block;\n      min-height: 1.25em;\n      text-align: left;\n    }\n    tr {\n      border-bottom: none;\n      padding: 0 10px;\n    }\n\n    /* sort out borders */\n    thead {\n      border: 0;\n      border-right: 1px solid $table-border-color;\n    }\n  }\n\n}\n\n\n// Collections\n.collection {\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border: 1px solid $collection-border-color;\n  border-radius: 2px;\n  overflow: hidden;\n  position: relative;\n\n  .collection-item {\n    background-color: $collection-bg-color;\n    line-height: $collection-line-height;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid $collection-border-color;\n\n    // Avatar Collection\n    &.avatar {\n      min-height: 84px;\n      padding-left: 72px;\n      position: relative;\n\n      // Don't style circles inside preloader classes.\n      &:not(.circle-clipper) > .circle,\n      :not(.circle-clipper) > .circle {\n        position: absolute;\n        width: 42px;\n        height: 42px;\n        overflow: hidden;\n        left: 15px;\n        display: inline-block;\n        vertical-align: middle;\n      }\n      i.circle {\n        font-size: 18px;\n        line-height: 42px;\n        color: #fff;\n        background-color: #999;\n        text-align: center;\n      }\n\n\n      .title {\n        font-size: 16px;\n      }\n\n      p {\n        margin: 0;\n      }\n\n      .secondary-content {\n        position: absolute;\n        top: 16px;\n        right: 16px;\n      }\n\n    }\n\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    &.active {\n      background-color: $collection-active-bg-color;\n      color: $collection-active-color;\n\n      .secondary-content {\n        color: #fff;\n      }\n    }\n  }\n  a.collection-item{\n    display: block;\n    transition: .25s;\n    color