ses($size, $i, $perc) {\n  &.offset-#{$size}#{$i} {\n    margin-left: $perc;\n  }\n  &.pull-#{$size}#{$i} {\n    right: $perc;\n  }\n  &.push-#{$size}#{$i} {\n    left: $perc;\n  }\n}\n\n\n.row {\n  margin-left: auto;\n  margin-right: auto;\n  margin-bottom: 20px;\n\n  // Clear floating children\n  &:after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n\n  .col {\n    float: left;\n    box-sizing: border-box;\n    padding: 0 $gutter-width / 2;\n    min-height: 1px;\n\n    &[class*=\"push-\"],\n    &[class*=\"pull-\"] {\n      position: relative;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      &.s#{$i} {\n        width: $perc;\n        @include reset-offset;\n      }\n      $i: $i + 1;\n    }\n\n    $i: 1;\n    @while $i <= $num-cols {\n      $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n      @include grid-classes(\"s\", $i, $perc);\n      $i: $i + 1;\n    }\n\n    @media #{$medium-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.m#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"m\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.l#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"l\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$extra-large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols 