s-color;\n  }\n\n  // Hide helper text on data message\n  &.valid ~ .helper-text[data-success],\n  &:focus.valid ~ .helper-text[data-success],\n  &.invalid ~ .helper-text[data-error],\n  &:focus.invalid ~ .helper-text[data-error] {\n    @extend %hidden-text;\n  }\n\n  // Valid Input Style\n  &.valid,\n  &:focus.valid {\n    @extend %valid-input-style;\n  }\n\n  // Custom Success Message\n  &.valid ~ .helper-text:after,\n  &:focus.valid ~ .helper-text:after {\n    @extend %custom-success-message;\n  }\n  &:focus.valid ~ label {\n    color: $input-success-color;\n  }\n\n  // Invalid Input Style\n  &.invalid,\n  &:focus.invalid {\n    @extend %invalid-input-style;\n  }\n\n  // Custom Error message\n  &.invalid ~ .helper-text:after,\n  &:focus.invalid ~ .helper-text:after {\n    @extend %custom-error-message;\n  }\n  &:focus.invalid ~ label {\n    color: $input-error-color;\n  }\n\n  // Full width label when using validate for error messages\n  &.validate + label {\n    width: 100%;\n  }\n\n  // Form Message Shared Styles\n  & + label:after {\n    @extend %input-after-style;\n  }\n}\n\n\n/* Validation Sass Placeholders */\n%valid-input-style {\n  border-bottom: 1px solid $input-success-color;\n  box-shadow: 0 1px 0 0 $input-success-color;\n}\n%invalid-input-style {\n  border-bottom: $input-invalid-border;\n  box-shadow: 0 1px 0 0 $input-error-color;\n}\n%hidden-text {\n  color: transparent;\n  user-select: none;\n  pointer-events: none;\n}\n%custom-success-message {\n  content: attr(data-success);\n  color: $input-success-color;\n}\n%custom-error-message {\n  content: attr(data-error);\n  color: $input-error-color;\n}\n%input-after-style {\n  display: block;\n  content: \"\";\n  position: absolute;\n  top: 100%;\n  left: 0;\n  opacity: 0;\n  transition: .2s opacity ease-out, .2s color ease-out;\n}\n\n\n// Styling for input field wrapper\n.input-field {\n  // Inline styles\n  &.inline {\n    display: inline-block;\n    vertical-align: middle;\n    margin-left: 5px;\n\n    input,\n    .select-dropdown {\n      margin-bottom: 1rem;\n    }\n  }\n\n  // Gutter spacing\n  &.col {\n    label {\n      left: $gutter-width / 2;\n    }\n\n    .prefix ~ label,\n    .prefix ~ .validate ~ label {\n      width: calc(100% - 3rem - #{$gutter-width});\n    }\n  }\n\n  position: relative;\n  margin-top: 1rem;\n  margin-bottom: 1rem;\n\n  & > label {\n    color: $input-border-color;\n    position: absolute;\n    top: 0;\n    left: 0;\n    font-size: 1rem;\n    cursor: text;\n    transition: transform .2s ease-out, color .2s ease-out;\n    transform-origin: 0% 100%;\n    text-align: initial;\n    transform: translateY(12px);\n\n    &:not(.label-icon).active {\n      transform: translateY(-14px) scale(.8);\n      transform-origin: 0 0;\n    }\n  }\n\n  // Autofill + date + time inputs\n  & > input[type]:-webkit-autofill:not(.browser-default):not([type=\"search\"]) + label,\n  & > input[type=date]:not(.browser-default) + label,\n  & > input[type=time]:not(.browser-default) + label {\n    transform: translateY(-14px) scale(.8);\n    transform-origin: 0 0;\n  }\n\n  .helper-text {\n    &::after {\n      opacity: 1;\n      position: absolute;\n      top: 0;\n      left: 0;\n    }\n\n    position: relative;\n    min-height: 18px;\n    display: block;\n    font-size: 12px;\n    color: rgba(0,0,0,.54);\n  }\n\n  // Prefix Icons\n  .prefix {\n    position: absolute;\n    width: $input-height;\n    font-size: $input-icon-size;\n    transition: color .2s;\n    top: ($input-height - $input-icon-size) / 2;\n\n    &.active { color: $input-focus-color; }\n  }\n\n  .prefix ~ input,\n  .prefix ~ textarea,\n  .prefix ~ label,\n  .prefix ~ .validate ~ label,\n  .prefix ~ .helper-text,\n  .prefix ~ .autocomplete-content {\n    margin-left: 3rem;\n    width: 92%;\n    width: calc(100% - 3rem);\n  }\n\n  .prefix ~ label { margin-left: 3rem; }\n\n  @media #{$medium-and-down} {\n    .prefix ~ input {\n      width: 86%;\n      width: calc(100% - 3rem);\n    }\n  }\n\n  @media #{$small-and-down} {\n    .prefix ~ input {\n      width: 80%;\n      width: calc(100% - 3rem);\n    }\n  }\n}\n\n\n/* Search Field */\n\n.input-field input[type=search] {\n  display: block;\n  line-height: inherit;\n  transition: .3s background-color;\n\n  .nav-wrapper & {\n    height: inherit;\n    padding-left: 4rem;\n    width: calc(100% - 4rem);\n    border: 0;\n    box-shadow: none;\n  }\n\n  &:focus:not(.browser-default) {\n    background-color: $input-background;\n    border: 0;\n    box-shadow: none;\n    color: #444;\n\n    & + label i,\n    & ~ .mdi-navigation-close,\n    & ~ .material-icons {\n      color: #444;\n    }\n  }\n\n  & + .label-icon {\n    transform: none;\n    left: 1rem;\n  }\n\n  & ~ .mdi-navigation-close,\n  & ~ .material-icons {\n    position: absolute;\n    top: 0;\n    right: 1rem;\n    color: transparent;\n    cursor: pointer;\n    font-size: $input-icon-size;\n    transition: .3s color;\n  }\n}\n\n\n/* Textarea */\n\n// Default textarea\ntextarea {\n  width: 100%;\n  height: $input-height;\n  background-color: transparent;\n\n  &.materialize-textarea {\n    line-height: normal;\n    overflow-y: hidden; /* prevents scroll bar flash */\n    padding: .8rem 0 .8rem 0; /* prevents text jump on Enter keypress */\n    resize: none;\n    min-height: $input-height;\n    box-sizing: border-box;\n  }\n}\n\n// For textarea autoresize\n.hiddendiv {\n  visibility: hidden;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */\n  padding-top: 1.2rem; /* prevents text jump on Enter keypress */\n\n  // Reduces repaints\n  position: absolute;\n  top: 0;\n  z-index: -1;\n}\n\n\n/* Autocomplete */\n.autocomplete-content {\n  li {\n    .highlight { color: #444; }\n\n    img {\n      height: $dropdown-item-height - 10;\n      width: $dropdown-item-height - 10;\n      margin: 5px 15px;\n    }\n  }\n}\n\n/* Character Counter */\n.character-counter {\n  min-height: 18px;\n}\n","/* Radio Buttons\n   ========================================================================== */\n\n// Remove default Radio Buttons\n[type=\"radio\"]:not(:checked),\n[type=\"radio\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n[type=\"radio\"]:not(:checked) + span,\n[type=\"radio\"]:checked + span {\n  position: relative;\n  padding-left: 35px;\n  cursor: pointer;\n  display: inline-block;\n  height: 25px;\n  line-height: 25px;\n  font-size: 1rem;\n  transition: .28s ease;\n  user-select: none;\n}\n\n[type=\"radio\"] + span:before,\n[type=\"radio\"] + span:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  margin: 4px;\n  width: 16px;\n  height: 16px;\n  z-index: 0;\n  transition: .28s ease;\n}\n\n/* Unchecked styles */\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after,\n[type=\"radio\"]:checked + span:before,\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border-radius: 50%;\n}\n\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after {\n  border: 2px solid $radio-empty-color;\n}\n\n[type=\"radio\"]:not(:checked) + span:after {\n  transform: scale(0);\n}\n\n/* Checked styles */\n[type=\"radio\"]:checked + span:before {\n  border: 2px solid transparent;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border: $radio-border;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:after {\n  background-color: $radio-fill-color;\n}\n\n[type=\"radio\"]:checked + span:after {\n  transform: scale(1.02);\n}\n\n/* Radio With gap */\n[type=\"radio\"].with-gap:checked + span:after {\n  transform: scale(.5);\n}\n\n/* Focused styles */\n[type=\"radio\"].tabbed:focus + span:before {\n  box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n}\n\n/* Disabled Radio With gap */\n[type=\"radio\"].with-gap:disabled:checked + span:before {\n  border: 2px solid $input-disabled-color;\n}\n\n[type=\"radio\"].with-gap:disabled:checked + span:after {\n  border: none;\n  background-color: $input-disabled-color;\n}\n\n/* Disabled style */\n[type=\"radio\"]:disabled:not(:checked) + span:before,\n[type=\"radio\"]:disabled:checked + span:before {\n  background-color: transparent;\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled + span {\n  color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:not(:checked) + span:before {\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:checked + span:after {\n  background-color: $input-disabled-color;\n  border-color: $input-disabled-solid-color;\n}\n","/* Checkboxes\n   ========================================================================== */\n\n/* Remove default checkbox */\n[type=\"checkbox\"]:not(:checked),\n[type=\"checkbox\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n// Checkbox Styles\n[type=\"checkbox\"] {\n  // Text Label Style\n  + span:not(.lever) {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n    user-select: none;\n  }\n\n  /* checkbox aspect */\n  + span:not(.lever):before,\n  &:not(.filled-in) + span:not(.lever):after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 3px;\n    transition: .2s;\n  }\n\n  &:not(.filled-in) + span:not(.lever):after {\n    border: 0;\n    transform: scale(0);\n  }\n\n  &:not(:checked):disabled + span:not(.lever):before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    transform: scale(1);\n    border: 0;\n    border-radius: 50%;\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n    background-color: rgba(0,0,0,.1);\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + span:not(.lever):before {\n    top: -4px;\n    left: -5px;\n    width: 12px;\n    height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: $radio-border;\n    border-bottom: $radio-border;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + span:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  + span:not(.lever):before {\n    top: -11px;\n    left: -12px;\n    width: 10px;\n    height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: $radio-border;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + span:not(.lever):before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + span:not(.lever):after {\n    border-radius: 2px;\n  }\n\n  + span:not(.lever):before,\n  + span:not(.lever):after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + span:not(.lever):before {\n    width: 0;\n    height: 0;\n    border: 3px solid transparent;\n    left: 6px;\n    top: 10px;\n    transform: rotateZ(37deg);\n    transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + span:not(.lever):after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + span:not(.lever):before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-background;\n      border-bottom: 2px solid $input-background;\n      transform: rotateZ(37deg);\n      transform-origin: 100% 100%;\n    }\n\n    + span:not(.lever):after {\n      top: 0;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    border-radius: 2px;\n    border-color: $radio-empty-color;\n    background-color: rgba(0,0,0,.1);\n  }\n\n  &.tabbed:checked:focus + span:not(.lever):after {\n    border-radius: 2px;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n  }\n\n  // Disabled style\n  &:disabled:not(:checked) + span:not(.lever):before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + span:not(.lever):after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + span:not(.lever):before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + span:not(.lever):after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n","/* Switch\n   ========================================================================== */\n\n.switch,\n.switch * {\n  -webkit-tap-highlight-color: transparent;\n  user-select: none;\n}\n\n.switch label {\n  cursor: pointer;\n}\n\n.switch label input[type=checkbox] {\n  opacity: 0;\n  width: 0;\n  height: 0;\n\n  &:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n\n    &:before, &:after {\n      left: 18px;\n    }\n\n    &:after {\n      background-color: $switch-bg-color;\n    }\n  }\n}\n\n.switch label .lever {\n  content: \"\";\n  display: inline-block;\n  position: relative;\n  width: 36px;\n  height: 14px;\n  background-color: $switch-unchecked-lever-bg;\n  border-radius: $switch-radius;\n  margin-right: 10px;\n  transition: background 0.3s ease;\n  vertical-align: middle;\n  margin: 0 16px;\n\n  &:before, &:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    border-radius: 50%;\n    left: 0;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease, transform .1s ease;\n  }\n\n  &:before {\n    background-color: transparentize($switch-bg-color, .85);\n  }\n\n  &:after {\n    background-color: $switch-unchecked-bg;\n    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);\n  }\n}\n\n// Switch active style\ninput[type=checkbox]:checked:not(:disabled) ~ .lever:active::before,\ninput[type=checkbox]:checked:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: transparentize($switch-bg-color, .85);\n}\n\ninput[type=checkbox]:not(:disabled) ~ .lever:active:before,\ninput[type=checkbox]:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: rgba(0,0,0,.08);\n}\n\n// Disabled Styles\n.switch input[type=checkbox][disabled] + .lever {\n  cursor: default;\n  background-color: rgba(0,0,0,.12);\n}\n\n.switch label input[type=checkbox][disabled] + .lever:after,\n.switch label input[type=checkbox][disabled]:checked + .lever:after {\n  background-color: $input-disabled-solid-color;\n}\n","/* Select Field\n   ========================================================================== */\n\nselect { display: none; }\nselect.browser-default { display: block; }\n\nselect {\n  background-color: $select-background;\n  width: 100%;\n  padding: $select-padding;\n  border: $select-border;\n  border-radius: $select-radius;\n  height: $input-height;\n}\n\n.select-label {\n  position: absolute;\n}\n\n.select-wrapper {\n  &.valid .helper-text[data-success],\n  &.invalid ~ .helper-text[data-error] {\n    @extend %hidden-text;\n  }\n\n  &.valid {\n    & > input.select-dropdown {\n      @extend %valid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-success-message;\n    }\n  }\n\n  &.invalid {\n    & > input.select-dropdown,\n    & > input.select-dropdown:focus {\n      @extend %invalid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-error-message;\n    }\n  }\n\n  &.valid + label,\n  &.invalid + label {\n    width: 100%;\n    pointer-events: none;\n  }\n\n  & + label:after {\n    @extend %input-after-style;\n  }\n\n  position: relative;\n\n  input.select-dropdown {\n    &:focus {\n      border-bottom: 1px solid $input-focus-color;\n    }\n    position: relative;\n    cursor: pointer;\n    background-color: transparent;\n    border: none;\n    border-bottom: $input-border;\n    outline: none;\n    height: $input-height;\n    line-height: $input-height;\n    width: 100%;\n    font-size: $input-font-size;\n    margin: $input-margin;\n    padding: 0;\n    display: block;\n    user-select:none;\n    z-index: 1;\n  }\n\n  .caret {\n    position: absolute;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    margin: auto 0;\n    z-index: 0;\n    fill: rgba(0,0,0,.87);\n  }\n\n  & + label {\n    position: absolute;\n    top: -26px;\n    font-size: $label-font-size;\n  }\n}\n\n// Disabled styles\nselect:disabled {\n  color: $input-disabled-color;\n}\n\n.select-wrapper.disabled {\n  + label {\n    color: $input-disabled-color;\n  }\n  .caret {\n    fill: $input-disabled-color;\n  }\n}\n\n.select-wrapper input.select-dropdown:disabled {\n  color: $input-disabled-color;\n  cursor: default;\n  user-select: none;\n}\n\n.select-wrapper i {\n  color: $select-disabled-color;\n}\n\n.select-dropdown li.disabled,\n.select-dropdown li.disabled > span,\n.select-dropdown li.optgroup {\n  color: $select-disabled-color;\n  background-color: transparent;\n}\n\nbody.keyboard-focused {\n  .select-dropdown.dropdown-content li:focus {\n    background-color: $select-option-focus;\n  }\n}\n\n.select-dropdown.dropdown-content {\n  li {\n    &:hover {\n      background-color: $select-option-hover;\n    }\n\n    &.selected {\n      background-color: $select-option-selected;\n    }\n  }\n}\n\n// Prefix Icons\n.prefix ~ .select-wrapper {\n  margin-left: 3rem;\n  width: 92%;\n  width: calc(100% - 3rem);\n}\n\n.prefix ~ label { margin-left: 3rem; }\n\n// Icons\n.select-dropdown li {\n  img {\n    height: $dropdown-item-height - 10;\n    width: $dropdown-item-height - 10;\n    margin: 5px 15px;\n    float: right;\n  }\n}\n\n// Optgroup styles\n.select-dropdown li.optgroup {\n  border-top: 1px solid $dropdown-hover-bg-color;\n\n  &.selected > span {\n    color: rgba(0, 0, 0, .7);\n  }\n\n  & > span {\n    color: rgba(0, 0, 0, .4);\n  }\n\n  & ~ li.optgroup-option {\n    padding-left: 1rem;\n  }\n}\n","/* File Input\n   ========================================================================== */\n\n.file-field {\n  position: relative;\n\n  .file-path-wrapper {\n    overflow: hidden;\n    padding-left: 10px;\n  }\n\n  input.file-path { width: 100%; }\n\n  .btn {\n    float: left;\n    height: $input-height;\n    line-height: $input-height;\n  }\n\n  span {\n    cursor: pointer;\n  }\n\n  input[type=file] {\n\n    // Needed to override webkit button\n    &::-webkit-file-upload-button {\n      display: none;\n    }\n\n    position: absolute;\n    top: 0;\n    right: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    font-size: 20px;\n    cursor: pointer;\n    opacity: 0;\n    filter: alpha(opacity=0);\n  }\n}\n","/* Range\n   ========================================================================== */\n\n.range-field {\n  position: relative;\n}\n\ninput[type=range],\ninput[type=range] + .thumb {\n  @extend .no-select;\n  cursor: pointer;\n}\n\ninput[type=range] {\n  position: relative;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  width: 100%;\n  margin: 15px 0;\n  padding: 0;\n\n  &:focus {\n    outline: none;\n  }\n}\n\ninput[type=range] + .thumb {\n  position: absolute;\n  top: 10px;\n  left: 0;\n  border: none;\n  height: 0;\n  width: 0;\n  border-radius: 50%;\n  background-color: $radio-fill-color;\n  margin-left: 7px;\n\n  transform-origin: 50% 50%;\n  transform: rotate(-45deg);\n\n  .value {\n    display: block;\n    width: 30px;\n    text-align: center;\n    color: $radio-fill-color;\n    font-size: 0;\n    transform: rotate(45deg);\n  }\n\n  &.active {\n    border-radius: 50% 50% 50% 0;\n\n    .value {\n      color: $input-background;\n      margin-left: -1px;\n      margin-top: 8px;\n      font-size: 10px;\n    }\n  }\n}\n\n// Shared\n@mixin range-track {\n  height: $track-height;\n  background: #c2c0c2;\n  border: none;\n}\n\n@mixin range-thumb {\n  border: none;\n  height: $range-height;\n  width: $range-width;\n  border-radius: 50%;\n  background: $radio-fill-color;\n  transition: box-shadow .3s;\n}\n\n// WebKit\ninput[type=range] {\n  -webkit-appearance: none;\n}\n\ninput[type=range]::-webkit-slider-runnable-track {\n  @include range-track;\n}\n\ninput[type=range]::-webkit-slider-thumb {\n  @include range-thumb;\n  -webkit-appearance: none;\n  background-color: $radio-fill-color;\n  transform-origin: 50% 50%;\n  margin: -5px 0 0 0;\n\n}\n\n.keyboard-focused input[type=range]:focus:not(.active)::-webkit-slider-thumb {\n  box-shadow: 0 0 0 10px rgba($radio-fill-color, .26);\n}\n\n// FireFox\ninput[type=range] {\n  /* fix for FF unable to apply focus style bug  */\n  border: 1px solid white;\n\n  /*required for proper track sizing in FF*/\n}\n\ninput[type=range]::-moz-range-track {\n  @include range-track;\n}