ch the start location of the arc\n *                                should rotate each time, 216 gives us a\n *                                5 pointed star shape (it's 360/5 * 3).\n *                                For a 7 pointed star, we might do\n *                                360/7 * 3 = 154.286)\n *      CONTAINERWIDTH = 28px\n *      SHRINK_TIME = 400ms\n */\n\n\n.preloader-wrapper {\n  display: inline-block;\n  position: relative;\n  width: 50px;\n  height: 50px;\n\n  &.small {\n    width: 36px;\n    height: 36px;\n  }\n\n  &.big {\n    width: 64px;\n    height: 64px;\n  }\n\n  &.active {\n    /* duration: 360 * ARCTIME / (ARCSTARTROT + (360-ARCSIZE)) */\n    -webkit-animation: container-rotate 1568ms linear infinite;\n    animation: container-rotate 1568ms linear infinite;\n  }\n}\n\n@-webkit-keyframes container-rotate {\n  to { -webkit-transform: rotate(360deg) }\n}\n\n@keyframes container-rotate {\n  to { transform: rotate(360deg) }\n}\n\n.spinner-layer {\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  opacity: 0;\n  border-color: $spinner-default-color;\n}\n\n.spinner-blue,\n.spinner-blue-only {\n  border-color: #4285f4;\n}\n\n.spinner-red,\n.spinner-red-only {\n  border-color: #db4437;\n}\n\n.spinner-yellow,\n.spinner-yellow-only {\n  border-color: #f4b400;\n}\n\n.spinner-green,\n.spinner-green-only {\n  border-color: #0f9d58;\n}\n\n/**\n * IMPORTANT NOTE ABOUT CSS ANIMATION PROPERTIES (keanulee):\n *\n * iOS Safari (tested on iOS 8.1) does not handle animation-delay very well - it doesn't\n * guarantee that the animation will start _exactly_ after that value. So we avoid using\n * animation-delay and instead set custom keyframes for each color (as redundant as it\n * seems).\n *\n * We write out each animation in full (instead of separating animation-name,\n * animation-duration, etc.) because under the polyfill, Safari does not recognize those\n * specific properties properly, treats them as -webkit-animation, and overrides the\n * other animation rules. See https://github.com/Polymer/platform/issues/53.\n */\n.active .spinner-layer.spinner-blue {\n  /* durations: 4 * ARCTIME */\n  -webkit-animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, blue-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, blue-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n.active .spinner-layer.spinner-red {\n  /* durations: 4 * ARCTIME */\n  -webkit-animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, red-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, red-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n.active .spinner-layer.spinner-yellow {\n  /* durations: 4 * ARCTIME */\n  -webkit-animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, yellow-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, yellow-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n.active .spinner-layer.spinner-green {\n  /* durations: 4 * ARCTIME */\n  -webkit-animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, green-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both, green-fade-in-out 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n.active .spinner-layer,\n.active .spinner-layer.spinner-blue-only,\n.active .spinner-layer.spinner-red-only,\n.active .spinner-layer.spinner-yellow-only,\n.active .spinner-layer.spinner-green-only {\n  /* durations: 4 * ARCTIME */\n  opacity: 1;\n  -webkit-animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: fill-unfill-rotate 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n@-webkit-keyframes fill-unfill-rotate {\n  12.5% { -webkit-transform: rotate(135deg);  } /* 0.5 * ARCSIZE */\n  25%   { -webkit-transform: rotate(270deg);  } /* 1   * ARCSIZE */\n  37.5% { -webkit-transform: rotate(405deg);  } /* 1.5 * ARCSIZE */\n  50%   { -webkit-transform: rotate(540deg);  } /* 2   * ARCSIZE */\n  62.5% { -webkit-transform: rotate(675deg);  } /* 2.5 * ARCSIZE */\n  75%   { -webkit-transform: rotate(810deg);  } /* 3   * ARCSIZE */\n  87.5% { -webkit-transform: rotate(945deg);  } /* 3.5 * ARCSIZE */\n  to    { -webkit-transform: rotate(1080deg); } /* 4   * ARCSIZE */\n}\n\n@keyframes fill-unfill-rotate {\n  12.5% { transform: rotate(135deg);  } /* 0.5 * ARCSIZE */\n  25%   { transform: rotate(270deg);  } /* 1   * ARCSIZE */\n  37.5% { transform: rotate(405deg);  } /* 1.5 * ARCSIZE */\n  50%   { transform: rotate(540deg);  } /* 2   * ARCSIZE */\n  62.5% { transform: rotate(675deg);  } /* 2.5 * ARCSIZE */\n  75%   { transform: rotate(810deg);  } /* 3   * ARCSIZE */\n  87.5% { transform: rotate(945deg);  } /* 3.5 * ARCSIZE */\n  to    { transform: rotate(1080deg); } /* 4   * ARCSIZE */\n}\n\n@-webkit-keyframes blue-fade-in-out {\n  from { opacity: 1; }\n  25% { opacity: 1; }\n  26% { opacity: 0; }\n  89% { opacity: 0; }\n  90% { opacity: 1; }\n  100% { opacity: 1; }\n}\n\n@keyframes blue-fade-in-out {\n  from { opacity: 1; }\n  25% { opacity: 1; }\n  26% { opacity: 0; }\n  89% { opacity: 0; }\n  90% { opacity: 1; }\n  100% { opacity: 1; }\n}\n\n@-webkit-keyframes red-fade-in-out {\n  from { opacity: 0; }\n  15% { opacity: 0; }\n  25% { opacity: 1; }\n  50% { opacity: 1; }\n  51% { opacity: 0; }\n}\n\n@keyframes red-fade-in-out {\n  from { opacity: 0; }\n  15% { opacity: 0; }\n  25% { opacity: 1; }\n  50% { opacity: 1; }\n  51% { opacity: 0; }\n}\n\n@-webkit-keyframes yellow-fade-in-out {\n  from { opacity: 0; }\n  40% { opacity: 0; }\n  50% { opacity: 1; }\n  75% { opacity: 1; }\n  76% { opacity: 0; }\n}\n\n@keyframes yellow-fade-in-out {\n  from { opacity: 0; }\n  40% { opacity: 0; }\n  50% { opacity: 1; }\n  75% { opacity: 1; }\n  76% { opacity: 0; }\n}\n\n@-webkit-keyframes green-fade-in-out {\n  from { opacity: 0; }\n  65% { opacity: 0; }\n  75% { opacity: 1; }\n  90% { opacity: 1; }\n  100% { opacity: 0; }\n}\n\n@keyframes green-fade-in-out {\n  from { opacity: 0; }\n  65% { opacity: 0; }\n  75% { opacity: 1; }\n  90% { opacity: 1; }\n  100% { opacity: 0; }\n}\n\n/**\n * Patch the gap that appear between the two adjacent div.circle-clipper while the\n * spinner is rotating (appears on Chrome 38, Safari 7.1, and IE 11).\n */\n.gap-patch {\n  position: absolute;\n  top: 0;\n  left: 45%;\n  width: 10%;\n  height: 100%;\n  overflow: hidden;\n  border-color: inherit;\n}\n\n.gap-patch .circle {\n  width: 1000%;\n  left: -450%;\n}\n\n.circle-clipper {\n  display: inline-block;\n  position: relative;\n  width: 50%;\n  height: 100%;\n  overflow: hidden;\n  border-color: inherit;\n\n  .circle {\n    width: 200%;\n    height: 100%;\n    border-width: 3px; /* STROKEWIDTH */\n    border-style: solid;\n    border-color: inherit;\n    border-bottom-color: transparent !important;\n    border-radius: 50%;\n    -webkit-animation: none;\n    animation: none;\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n  }\n\n  &.left .circle {\n    left: 0;\n    border-right-color: transparent !important;\n    -webkit-transform: rotate(129deg);\n    transform: rotate(129deg);\n  }\n  &.right .circle {\n    left: -100%;\n    border-left-color: transparent !important;\n    -webkit-transform: rotate(-129deg);\n    transform: rotate(-129deg);\n  }\n}\n\n\n\n.active .circle-clipper.left .circle {\n  /* duration: ARCTIME */\n  -webkit-animation: left-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: left-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n.active .circle-clipper.right .circle {\n  /* duration: ARCTIME */\n  -webkit-animation: right-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n  animation: right-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both;\n}\n\n@-webkit-keyframes left-spin {\n  from { -webkit-transform: rotate(130deg); }\n  50% { -webkit-transform: rotate(-5deg); }\n  to { -webkit-transform: rotate(130deg); }\n}\n\n@keyframes left-spin {\n  from { transform: rotate(130deg); }\n  50% { transform: rotate(-5deg); }\n  to { transform: rotate(130deg); }\n}\n\n@-webkit-keyframes right-spin {\n  from { -webkit-transform: rotate(-130deg); }\n  50% { -webkit-transform: rotate(5deg); }\n  to { -webkit-transform: rotate(-130deg); }\n}\n\n@keyframes right-spin {\n  from { transform: rotate(-130deg); }\n  50% { transform: rotate(5deg); }\n  to { transform: rotate(-130deg); }\n}\n\n#spinnerContainer.cooldown {\n  /* duration: SHRINK_TIME */\n  -webkit-animation: container-rotate 1568ms linear infinite, fade-out 400ms cubic-bezier(0.4, 0.0, 0.2, 1);\n  animation: container-rotate 1568ms linear infinite, fade-out 400ms cubic-bezier(0.4, 0.0, 0.2, 1);\n}\n\n@-webkit-keyframes fade-out {\n  from { opacity: 1; }\n  to { opacity: 0; }\n}\n\n@keyframes fade-out {\n  from { opacity: 1; }\n  to { opacity: 0; }\n}\n",".screen-reader-text {\n  clip: rect(1px, 1px, 1px, 1px);\n  clip-path: polygon(0 0, 0 0, 0 0, 0 0);\n  position: absolute !important;\n  white-space: nowrap;\n  height: 1px;\n  width: 1px;\n  overflow: hidden;\n\n  &:focus {\n    clip: auto;\n    clip-path: none;\n    display: block;\n    height: auto;\n    left: 5px;\n    top: 5px;\n    width: auto;\n    z-index: 100000; /* Above WP toolbar. */\n  }\n}\n\n.a11y-tools-trigger-wrapper {\n  position: absolute;\n  right: 5%;\n  top: 5px;\n  transition: top 0.3s ease;\n\n  @media #{$small-and-down} {\n    .a11y-tools-active & {\n      top: 125px;\n    }\n  }\n\n  input:focus + label {\n    outline: $color-grey-dark auto 5px;\n  }\n\n  label {\n    display: block;\n    background: $color-black;\n    border-radius: 100%;\n    width: 50px;\n    height: 50px;\n    text-align: center;\n    cursor: pointer;\n    transition: background 0.3s ease;\n\n    i {\n      position: absolute;\n      left: 0;\n      width: 100%;\n      top: 50%;\n      transform: translateY(-50%);\n      color: $color-white;\n    }\n\n    span {\n      display: none;\n    }\n\n    &:hover {\n      // background: $color-green;\n    }\n\n    html[data-contrast=\"true\"] & {\n      border: 3px solid $color-white;\n\n      &:hover {\n        background: $color-white;\n\n        i {\n          color: $color-black;\n        }\n      }\n    }\n  }\n}\n\n.a11y-tools {\n  height: 50px;\n  overflow: hidden;\n  transition: height 0.3s ease;\n  position: relative;\n\n  @media #{$small-and-down} {\n    height: 0;\n\n    .a11y-tools-active & {\n      height: 110px;\n    }\n  }\n\n  html[data-contrast=\"true\"] & {\n    border-bottom: 1px solid $color-white;\n  }\n\n  .container {\n    @media #{$large-and-up} {\n      padding-right: 15px;\n    }\n  }\n\n  fieldset {\n    border: none;\n    padding: 0;\n    margin: 0;\n\n    @media #{$small-and-down} {\n      padding-bottom: 10px;\n    }\n\n    legend, & > div {\n      display: block;\n      float: left;\n      padding: 0 3px;\n    }\n\n    legend {\n      width: 65px;\n      font-size: 12px;\n      padding-top: 8px;\n\n      @media #{$xsmall-and-down} {\n        display: none;\n      }\n    }\n\n    label {\n      display: block;\n      text-indent: -100em;\n      overflow: hidden;\n      width: 50px;\n      height: 50px;\n      position: relative;\n      background: $color-grey-light;\n      transition: background 0.3s ease;\n      cursor: pointer;\n\n      &::after {\n        position: absolute;\n        left: 0;\n        width: 100%;\n        color: $color-black;\n      }\n\n      &:hover {\n        background: $color-grey-medium;\n\n        &::after {\n          color: $color-white;\n        }\n      }\n\n      html[data-contrast=\"true\"] & {\n        background: $color-black;\n        border: 1px solid $color-white;\n\n        &::after {\n          color: $color-white;\n        }\n\n        &:hover, &:focus {\n          background: $color-white;\n\n          &::after {\n            color: $color-black;\n          }\n        }\n      }\n    }\n\n    input {\n      &[type=\"radio\"] {\n        &:checked + label {\n          // background: $color-green-dark;\n\n          &::after {\n            color: $color-white;\n          }\n\n          html[data-contrast=\"true\"] & {\n            background: $color-grey-dark;\n          }\n        }\n\n        &:focus + label {\n          outline: $color-grey-dark auto 5px;\n\n          html[data-contrast=\"true\"] & {\n            outline: $color-white auto 5px;\n          }\n        }\n      }\n\n      &[type=\"checkbox\"] {\n        &:checked + label {\n          // background: $color-green-dark;\n\n          &::after {\n            background-image: image('icon-contrast-inverse.svg');\n          }\n\n          html[data-contrast=\"true\"] & {\n            background: $color-grey-dark;\n\n            &:hover, &:focus {\n              background: $color-grey-medium;\n            }\n          }\n        }\n\n        &:focus + label {\n          outline: $color-grey-dark auto 5px;\n\n          html[data-contrast=\"true\"] & {\n            outline: $color-white auto 5px;\n          }\n        }\n      }\n    }\n\n    &.toggle-contrast {\n      label {\n        &::after {\n          content: '';\n          top: 0;\n          height: 100%;\n          background: image('icon-contrast.svg') no-repeat 50% 50%;\n        }\n      }\n    }\n\n    &.text-size {\n      label {\n        &::after {\n          content: 'A';\n          top: 50%;\n          transform: translateY(-50%);\n          text-indent: 0;\n          text-align: center;\n          transition: color 0.3s ease;\n          speak: none;\n        }\n      }\n\n      div[class*=\"default-\"] label::af