;\n      }\n\n      i {\n        font-size: 2em;\n        line-height: 1.5em;\n      }\n\n      html[data-contrast=\"true\"] & {\n        color: $color-white;\n      }\n    }\n  }\n}\n\nnav {\n  ul {\n    a {\n      text-decoration: none;\n      font-weight: 300;\n      padding: 1rem 15px;\n      color: $color-black;\n      font-size: 17px;\n\n      [data-font=\"3\"] & {\n        font-size: 1.2rem;\n      }\n\n      .header-inline & {\n        padding: 1rem 15px;\n      }\n    }\n  }\n}\n\n.navbar-menu {\n  border: none;\n  margin: 0;\n  box-shadow: none;\n  padding: 0 15px;\n\n  & > ul {\n    flex: 1 1 auto;\n  }\n\n  li {\n    padding: 10px 0;\n    height: 100%;\n\n    &::before {\n      display: none;\n    }\n\n    &:hover, &:focus, &:active {\n      background-color: #F3F3F3;\n\n      > a {\n        background-color: #F3F3F3;\n      }\n    }\n\n    &.menu-item-has-children {\n      a {\n        cursor: default;\n\n        @media #{$medium-and-down} {\n          cursor: pointer;\n        }\n      }\n\n      .sub-menu {\n        margin-top: 10px;\n        max-height: 0;\n        overflow: hidden;\n        transition: max-height 0.3s ease;\n        // background: $color-blue-light;\n\n        li {\n          float: none;\n          line-height: 0;\n\n          &:hover, &:focus, &:hover {\n            background: #F5F5F5;\n          }\n        }\n\n        a {\n          cursor: pointer;\n          position: relative;\n\n          &::after {\n            content: '';\n            background-image: image('arrow-right.svg');\n            position: absolute;\n            right: 30px;\n            top: 10px;\n            z-index: 1000;\n            width: 20px;\n            opacity: 0;\n            height: 20px;\n            background-size: contain;\n            background-repeat: no-repeat;\n            transition: all 0.2s;\n          }\n\n          &:hover, &:focus, &:hover {\n            background: transparent;\n\n            &::after {\n              right: 10px;